import { Component, OnInit } from '@angular/core';
import { RaceModel } from '../model/race.model';
import { RaceDataService } from '../race-data.service';
import { RaceAppComponent } from '../race-app/race-app.component';

@Component({
  selector: 'races-list',
  templateUrl: './races-list.component.html',
  styleUrls: ['./races-list.component.css']
})
export class RacesListComponent implements OnInit {

  races: RaceModel[];

  constructor(
      private raceDataService: RaceDataService,
      private raceAppComponent: RaceAppComponent
  ) { }

    ngOnInit() {
        this.races = this.raceDataService.getRaces();

    }

  getTotalSpent(){
      let totalSpent = 0;
      this.races.forEach(function(race){
          if(race.entered){
              totalSpent += race.price;
          }
      });
      return totalSpent;
  }

  enterRace(race) {
      if(this.raceAppComponent.raceBudget > race.price) {
          race.entered = true;
          this.raceAppComponent.raceBudget = this.raceAppComponent.raceBudget - race.price;
      } else {
          alert('not enough money');
      }
  }

  leaveRace(race) {
      race.entered = false;
      this.raceAppComponent.raceBudget = this.raceAppComponent.raceBudget + race.price;
  }
}



