import { BrowserModule } from '@angular/platform-browser';
import { NgModule } from '@angular/core';
import { FormsModule } from '@angular/forms';
import { HttpModule } from '@angular/http';
import { RouterModule } from '@angular/router';

import { AppComponent } from './app.component';
import { RaceAppComponent } from './race-app/race-app.component';
import { RacesListComponent } from './races-list/races-list.component';
import { RaceComponent } from './race/race.component';
import { RaceDataService } from './race-data.service';
import { RaceDetailComponent } from './race-detail/race-detail.component';

@NgModule({
  declarations: [
    AppComponent,
    RaceAppComponent,
    RacesListComponent,
    RaceComponent,
    RaceDetailComponent
  ],
  imports: [
    BrowserModule,
    FormsModule,
    HttpModule,
    RouterModule.forRoot([
        { path: '', redirectTo: 'race-list', pathMatch: 'full' },
        { path: 'race-list', component: RacesListComponent },
        { path: 'race/:id', component: RaceDetailComponent }
    ])
  ],
  providers: [ RaceDataService ],
  bootstrap: [ RaceAppComponent ]
})
export class AppModule { }



